import {debounce} from 'lodash';

import React, {Component} from 'react';

class PeekAttributes extends Component {
    state = {
        left: 0,
        top: 0
    }

    componentDidMount() {
        const update  = (e) => {
            this.setState({left: e.pageX, top: e.pageY});
        }

        const debouncedMM = debounce(update, 300);

        document.addEventListener('mousemove', debouncedMM);
    }
    render() {
        return (
            <div>Attributes</div>
        )
    }
}

export default PeekAttributes;